import { observer } from "mobx-react-lite";
import { useForm } from "react-hook-form";
import { FormControl, Button, Input, FormErrorMessage } from "@chakra-ui/react";
import { Link, useNavigate } from "react-router-dom";

import { RegistrationUserSchema } from "../../generated";
import { useApp } from "../../core/useApp";
import { showToast } from "../../uikit/tost";
import * as S from "./styled";

const LoginPage = () => {
  const { auth } = useApp();
  const navigate = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<RegistrationUserSchema>();

  const handleLogin = handleSubmit(async ({ email, password }) => {
    try {
      await auth.login(email, password);
      navigate("/", { replace: true });
    } catch {
      showToast("Something is wrong");
    }
  });

  return (
    <S.Content>
      <S.CardForm onSubmit={handleLogin}>
        <img src="assets/logo.svg" alt="logo" width={200} />
        <br />

        <FormControl isInvalid={errors.email != null}>
          <Input id="email" type="email" placeholder="Email" {...register("email", { required: true })} />
        </FormControl>

        <FormControl isInvalid={errors.password != null}>
          <Input
            id="password"
            type="password"
            placeholder="Password"
            {...register("password", {
              required: true,
              minLength: { value: 6, message: "Minimum length should be 6" },
            })}
          />
          <FormErrorMessage>{errors.password && errors.password.message}</FormErrorMessage>
        </FormControl>

        <S.NoAccount>
          Don't have an account yet? <Link to="/register">Register</Link>
        </S.NoAccount>

        <Button mt={4} width="100%" colorScheme="purple" isLoading={isSubmitting} type="submit">
          Login
        </Button>
      </S.CardForm>
    </S.Content>
  );
};

export default observer(LoginPage);
