import { action, computed, makeObservable, observable, runInAction } from "mobx";
import { CreatePaymentsRequestSchema, ListPaymentResponseSchema, PaymentsService } from "../generated";
import { showToast } from "../uikit/tost";

class IncomeModel {
  lastRefresh = 0;
  payments: ListPaymentResponseSchema[] = [];

  constructor() {
    makeObservable(this, {
      payments: observable,
      totalReceived: computed,
      removePayment: action,
      createPayment: action,
      refresh: action,
    });
  }

  get totalReceived() {
    return this.payments.reduce((acc, p) => acc + p.amount_usd, 0);
  }

  getPaymentStatus(payment: ListPaymentResponseSchema) {
    switch (payment.status) {
      case 1:
        return { status: "Done", indicator: "#00ba88" };
      case 2:
        return { status: "Failed", indicator: "tomato" };
      default:
        return { status: "Pending", indicator: "#ccc" };
    }
  }

  async createPayment(data: CreatePaymentsRequestSchema): Promise<string | null> {
    const payment = {
      amount_usd: Number(data.amount_usd),
      name: data.name,
      status: 0,
      created_at: new Date().toISOString(),
      received_network: "mainnet",
      description: data.description ?? "",
      display_id: String(Date.now()),
      expiry_at: new Date().toISOString(),
      received_currency: data.coin ?? "NEAR",
    };

    runInAction(() => this.payments.push(payment));
    const result = await PaymentsService.createPaymentApiV1PaymentsPost(data).catch(() => {
      this.removePayment(payment.display_id);
      showToast("Payment not created");
    });

    return result?.url ?? null
  }

  removePayment(id: string) {
    const index = this.payments.findIndex((p) => p.display_id === id);
    if (index !== -1) this.payments.splice(index, 1);
  }

  async refresh() {
    if (Date.now() - this.lastRefresh < 60000) return;
    this.lastRefresh = Date.now();

    const list = await PaymentsService.listUserPaymentsApiV1PaymentsListGet(true);
    runInAction(() => {
      this.payments = list;
    });
  }
}

export default IncomeModel;
