import { makeObservable, observable, runInAction } from "mobx";
import { ApiClientsService, CreateApiClientInputSchema, RetrieveApiClientReturnSchema } from "../generated";
import { showToast } from "../uikit/tost";
import { CompanyStore } from "./App";

class Integrations {
  public list: (RetrieveApiClientReturnSchema & { loading: boolean, id: number })[] = [];

  constructor(readonly company: CompanyStore) {
    makeObservable(this, { list: observable });
    this.fetch();
  }

  async create(data: Omit<CreateApiClientInputSchema, 'beneficiary_id'>) {
    await ApiClientsService.createApiClientApiV1ApiClientsPost({ ...data, beneficiary_id: this.company.model.id })
    await this.fetch()
  }

  async generateSecret(id: number) {
    const api = this.list.find(api => api.id === id)
    if (api == null) return;
    api.loading = true

    const { client_secret } = await ApiClientsService.generateNewClientSecretApiV1ApiClientsClientIdGenerateNewSecretPatch(id)
    runInAction(() => {
      if (api == null) return;
      api.client_secret = client_secret
      api.loading = false
    })

    await navigator.clipboard.writeText(client_secret);
    showToast("The new secret has been copied, you need to save it");
  }

  async fetch() {
    const list = await ApiClientsService.listClientsApiV1ApiClientsGet()
    console.log(list)
    const apis: (RetrieveApiClientReturnSchema & { loading: boolean, id: number })[] = []

    const promises = list.map(async ({ id }, i) => {
      const api = await ApiClientsService.retrieveClientApiV1ApiClientsClientIdDetailGet(id)
      apis[i] = Object.assign({ loading: false, id }, api)
    })

    await Promise.all(promises)
    runInAction(() => {
      this.list = apis.filter(v => v)
    });
  }
}

export default Integrations;
