import { observer } from "mobx-react-lite";
import { useForm } from "react-hook-form";
import { FormControl, Button, Input, Select, FormErrorMessage } from "@chakra-ui/react";
import { Link, useNavigate } from "react-router-dom";

import { RegistrationUserSchema } from "../../generated";
import { useApp } from "../../core/useApp";
import { showToast } from "../../uikit/tost";
import * as S from "./styled";

const RegisterPage = () => {
  const { auth } = useApp();
  const navigate = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<RegistrationUserSchema>({
    defaultValues: { local_currency: "EUR" },
  });

  const handleSignup = handleSubmit(async (data) => {
    try {
      await auth.signup(data);
      navigate("/", { replace: true });
    } catch {
      showToast("Something is wrong");
    }
  });

  return (
    <S.Content>
      <S.CardForm onSubmit={handleSignup}>
        <img src="assets/logo.svg" alt="logo" width={200} />
        <br />

        <FormControl isInvalid={errors.user_name != null}>
          <Input id="user_name" placeholder="Firstname lastname" {...register("user_name", { required: true })} />
        </FormControl>

        <FormControl isInvalid={errors.email != null}>
          <Input id="email" type="email" placeholder="Email" {...register("email", { required: true })} />
        </FormControl>

        <FormControl isInvalid={errors.password != null}>
          <Input
            id="password"
            type="password"
            placeholder="Password"
            {...register("password", {
              required: true,
              minLength: { value: 6, message: "Minimum length should be 6" },
            })}
          />
          <FormErrorMessage>{errors.password && errors.password.message}</FormErrorMessage>
        </FormControl>

        <FormControl isInvalid={errors.company_name != null} mt={4}>
          <Input id="company_name" placeholder="Company name" {...register("company_name", { required: true })} />
        </FormControl>

        <FormControl isInvalid={errors.company_country_of_residence != null}>
          <Input
            placeholder="Company country"
            id="company_country_of_residence"
            {...register("company_country_of_residence", { required: true })}
          />
        </FormControl>

        <FormControl isInvalid={errors.local_currency != null}>
          <Select
            id="local_currency"
            placeholder="Select currency"
            {...register("local_currency", { required: "This is required" })}
          >
            <option value="USD">USD</option>
            <option value="EUR">EUR</option>
          </Select>
        </FormControl>

        <S.NoAccount>
          Do you already have an account? <Link to="/login">Login</Link>
        </S.NoAccount>

        <Button mt={4} width="100%" colorScheme="purple" isLoading={isSubmitting} type="submit">
          Signup
        </Button>
      </S.CardForm>
    </S.Content>
  );
};

export default observer(RegisterPage);
