import StartToastifyInstance from "toastify-js";

export const showToast = (text: string) =>
  StartToastifyInstance({
    text,
    duration: 3000,
    gravity: "bottom",
    position: "left",
    style: {
      fontSize: "18px",
      background: "#0b293d",
      borderRadius: "12px",
    },
  }).showToast();
