import { ReactComponent as AccountIcon } from "./account.svg";
import { ReactComponent as AnalyticsIcon } from "./analytics.svg";
import { ReactComponent as IncomeIcon } from "./income.svg";
import { ReactComponent as IntegrationsIcon } from "./integrations.svg";
import { ReactComponent as ProfileIcon } from "./profile.svg";
import { ReactComponent as PaymentsIcon } from "./payments.svg";

const icons: Record<string, React.FunctionComponent> = {
  account: AccountIcon,
  analytics: AnalyticsIcon,
  income: IncomeIcon,
  integrations: IntegrationsIcon,
  profile: ProfileIcon,
  payments: PaymentsIcon,
};

export const Icon = ({ name }: { name: string }) => {
  const IconM = icons[name] ?? (() => name);
  return <IconM />;
};
