import {
  FormControl,
  FormHelperText,
  FormLabel,
  HStack,
  Input,
  InputGroup,
  Button,
  InputLeftAddon,
  Select,
  VStack,
  Flex,
} from "@chakra-ui/react";
import { observer } from "mobx-react-lite";
import { useApp, useCompany } from "../../core/useApp";
import ChangePassword from "./ChangePassword";
import * as S from "./styled";

const Profile = () => {
  const { auth, account } = useApp();
  const { settings, model } = useCompany();

  return (
    <S.PageHeader>
      <S.Section>
        <S.Headline>Account</S.Headline>
        <HStack spacing={4} width="100%">
          <FormControl>
            <FormLabel htmlFor="email">ontact email</FormLabel>
            <Input id="email" value={settings.profile.email} isReadOnly />
          </FormControl>

          <FormControl>
            <FormLabel htmlFor="username">Username</FormLabel>
            <Input id="username" value={settings.profile.username} isReadOnly />
          </FormControl>
        </HStack>

        <Flex mt={4} gap={4}>
          <ChangePassword />
          <Button variant="outline" colorScheme="purple" onClick={() => auth.logout()}>
            Logout
          </Button>
        </Flex>
      </S.Section>

      <S.Section>
        <S.Headline>Company settings</S.Headline>
        <VStack spacing={2} alignItems="flex-start">
          <HStack spacing={4} width="100%">
            <FormControl>
              <FormLabel htmlFor="name">Name</FormLabel>
              <Input id="name" value={model.name} isReadOnly />
            </FormControl>

            <FormControl>
              <FormLabel htmlFor="contry">Country</FormLabel>
              <Input id="contry" value={model.country_of_residence} isReadOnly />
            </FormControl>
          </HStack>

          <FormControl>
            <FormLabel>Wallets</FormLabel>

            {account?.currencies.list.map((cur) => (
              <InputGroup key={cur.ticker} mb={2}>
                <InputLeftAddon width={"80px"} children={cur.ticker} />
                <Input
                  type="text"
                  autoComplete="off"
                  placeholder="Wallet address"
                  defaultValue={settings.getWallet(cur.ticker)}
                />
              </InputGroup>
            ))}
          </FormControl>

          <FormControl width="calc(50% - 8px)">
            <FormLabel htmlFor="coin">Local currency</FormLabel>
            <Select id="coin" placeholder="Select coin" value={model.local_currency}>
              <option value="USD">USD</option>
              <option value="EUR">EUR</option>
            </Select>
            <FormHelperText>
              The value of the cryptocurrency in your account is also represented in your local currency.
            </FormHelperText>
          </FormControl>
        </VStack>
      </S.Section>
    </S.PageHeader>
  );
};

export default observer(Profile);
