import React, { FC } from "react";
import { useLocation, matchPath, Outlet, Navigate } from "react-router-dom";
import { observer } from "mobx-react-lite";
import { Tag, TagLabel } from "@chakra-ui/react";

import CompaniesPicker from "../Companies/CompaniesPicker";
import { useApp } from "../../core/useApp";
import { Icon } from "../../uikit/icons";
import * as S from "./styled";

const routes: Record<string, any> = {
  "/account": { title: "Account" },
  "/income": { title: "Income" },
  "/analytics": { title: "Analytics" },
  "/profile": { title: "Profile" },
  "/integrations": { title: "Integrations" },
  "/payments": { title: "Payments" },
};

const Layout = () => {
  const location = useLocation();
  const { account } = useApp();
  const company = account?.company;

  if (account == null) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  if (company == null) {
    return null;
  }

  return (
    <S.Layout>
      <S.Header>
        <h2>{routes[location.pathname]?.title}</h2>
        <CompaniesPicker />
      </S.Header>

      <S.Sidebar>
        <S.SidebarLogo>
          <img src="assets/logo.svg" alt="logo" width={140} />
        </S.SidebarLogo>

        <S.SidebarSection>Dashboard</S.SidebarSection>
        <SidebarItem icon="account" to="/account">
          Account
        </SidebarItem>
        <SidebarItem icon="income" to="/income">
          Income
        </SidebarItem>
        <SidebarItem icon="analytics" to="/analytics">
          Analytics
        </SidebarItem>

        <S.SidebarSection>Settings</S.SidebarSection>
        <SidebarItem icon="payments" to="/payments">
          Payments
        </SidebarItem>
        <SidebarItem icon="integrations" to="/integrations">
          Integrations
          <Tag size="md" colorScheme="green" borderRadius="full" mt={1}>
            <TagLabel>{company.integrations.list.length}</TagLabel>
          </Tag>
        </SidebarItem>
        <SidebarItem icon="profile" to="/profile">
          Profile
        </SidebarItem>
      </S.Sidebar>

      <S.Page>
        <Outlet context={company} />
      </S.Page>
    </S.Layout>
  );
};

const SidebarItem: FC<{ to: string; children: React.ReactNode; icon: string }> = ({ to, icon, children }) => {
  const location = useLocation();

  return (
    <S.SidebarItem to={to} $selected={matchPath(to, location.pathname) != null}>
      <Icon name={icon} />
      {children}
    </S.SidebarItem>
  );
};

export default observer(Layout);
