import { observer } from "mobx-react-lite";
import { useEffect } from "react";

import CreatePaymentWidget from "../../common/CreatePaymentWidget";
import { useCompany } from "../../core/useApp";
import * as S from "./styled";

const Income = () => {
  const { income } = useCompany();

  useEffect(() => {
    income.refresh();
  }, [income]);

  return (
    <S.Page>
      <S.PageHeader>
        <h2>Total Balance: 70232</h2>
        <CreatePaymentWidget model={income} />
      </S.PageHeader>
      <S.Content>
        <S.Income>${income.totalReceived}</S.Income>

        <S.Table>
          {income.payments.map((payment) => {
            const { status, indicator } = income.getPaymentStatus(payment);

            return (
              <S.TableRow key={payment.display_id}>
                <S.PaymentStatus style={{ "--indicator": indicator } as any}>{status}</S.PaymentStatus>
                <S.PaymentAmount>${payment.amount_usd}</S.PaymentAmount>
                <S.PaymentAmount>
                  {payment.received_amount ?? 0} {payment.received_currency}
                </S.PaymentAmount>
                <S.PaymentDate>{new Date(payment.created_at).toLocaleString()}</S.PaymentDate>
              </S.TableRow>
            );
          })}
        </S.Table>
      </S.Content>
    </S.Page>
  );
};

export default observer(Income);
