/* istanbul ignore file */
/* tslint:disable */
/* eslint-disable */
import type { ConnectWalletSchema } from '../models/ConnectWalletSchema';
import type { RetrieveWallet } from '../models/RetrieveWallet';

import type { CancelablePromise } from '../core/CancelablePromise';
import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

export class WalletsService {

    /**
     * Add Supported Currency To Client
     * Connect wallet to system or user client
     * @param requestBody
     * @param clientId
     * @returns any Successful Response
     * @throws ApiError
     */
    public static addSupportedCurrencyToClientApiV1WalletsConnectWalletPost(
        requestBody: ConnectWalletSchema,
        clientId?: number,
    ): CancelablePromise<any> {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/v1/wallets/connect_wallet',
            query: {
                'client_id': clientId,
            },
            body: requestBody,
            mediaType: 'application/json',
            errors: {
                422: `Validation Error`,
            },
        });
    }

    /**
     * Delete Wallet From Client
     * Delete wallet from user or system client
     * @param walletId
     * @returns any Successful Response
     * @throws ApiError
     */
    public static deleteWalletFromClientApiV1WalletsDeleteWalletPost(
        walletId: number,
    ): CancelablePromise<any> {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/v1/wallets/delete_wallet',
            query: {
                'wallet_id': walletId,
            },
            errors: {
                422: `Validation Error`,
            },
        });
    }

    /**
     * List Wallets
     * If beneficiary id is specified then list wallets of system client of
     * specified beneficiary
     *
     * Else if no beneficiary specified list wallets of system client of
     * user's individual beneficiary
     * @param beneficiaryId
     * @returns RetrieveWallet Successful Response
     * @throws ApiError
     */
    public static listWalletsApiV1WalletsListGet(
        beneficiaryId?: string,
    ): CancelablePromise<Array<RetrieveWallet>> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/v1/wallets/list/',
            query: {
                'beneficiary_id': beneficiaryId,
            },
            errors: {
                422: `Validation Error`,
            },
        });
    }

}
