import axios from "axios";
import { action, makeObservable, observable } from "mobx";
import { OpenAPI, RegistrationUserSchema, UsersService } from "../generated";
import { parseJwt } from "./utils";

interface AccountCreds {
  accessToken: string;
  refreshToken: string;
}

class Auth {
  private account: AccountCreds | null = null;
  public isAuth = false;

  constructor() {
    makeObservable(this, {
      isAuth: observable,
      createAccount: action,
      logout: action,
    });

    OpenAPI.TOKEN = async (request) => {
      // without auth
      if (request.url === "/api/v1/users/refresh") return "";
      const token = await this?.getAccessToken();
      return token ?? "";
    };

    axios.interceptors.response.use(
      (response) => response,
      (error) => {
        if (error.response.status === 401) this.logout();
        return error;
      }
    );

    // TODO: think about security
    try {
      const session = JSON.parse(window.localStorage.getItem("session") ?? "");
      if (session.accessToken != null && session.refreshToken != null) {
        this.createAccount(session.accessToken, session.refreshToken);
      }
    } catch { }
  }

  createAccount(accessToken: string, refreshToken: string) {
    window.localStorage.setItem("session", JSON.stringify({ accessToken, refreshToken }));
    this.account = { accessToken, refreshToken };
    this.isAuth = true;
  }

  async login(email: string, password: string) {
    if (this.account != null) {
      this.logout();
    }

    const { access_token, refresh_token } = await UsersService.loginApiV1UsersLoginPost({ email, password });
    this.createAccount(access_token, refresh_token);
  }

  async signup(info: RegistrationUserSchema) {
    if (this.account != null) {
      this.logout();
    }

    const { access_token, refresh_token } = await UsersService.signupApiV1UsersSignupPost(info);
    this.createAccount(access_token, refresh_token);
  }

  // Singlton process for refresh access token
  private _refreshingTask?: Promise<void>
  async refreshingTask() {
    if (this._refreshingTask) {
      await this._refreshingTask;
      return;
    }

    this._refreshingTask = new Promise<void>(async (resolve) => {
      if (this.account == null) return null;
      try {
        const data = await UsersService.refreshApiV1UsersRefreshPost({ token: this.account.refreshToken })
        this.createAccount(data.access_token, data.refresh_token);
        resolve()
      } catch {
        resolve()
      }
    })

    await this._refreshingTask;
  }

  async getAccessToken() {
    if (this.account == null) return null;
    const jwt = parseJwt(this.account.accessToken);

    if (Math.ceil(Date.now() / 1000) >= jwt.exp) {
      await this.refreshingTask()
    }

    return this.account.accessToken;
  }

  logout() {
    window.localStorage.removeItem("session");
    this.isAuth = false;
    this.account = null;
  }
}

export default Auth;
