import { action, makeObservable, observable, runInAction } from "mobx";
import { RetrieveWallet, UsersService, WalletsService } from "../generated";
import { ModelStatus } from "./common";

class AccountSettings {
  public status = ModelStatus.Ready;
  public profile = {
    email: "",
    username: "",
    companyName: "",
    companyCountry: "",
  };

  public wallets: RetrieveWallet[] = []

  constructor() {
    makeObservable(this, {
      profile: observable,
      changeProfile: action,
    });
  }

  changeProfile(profile: Partial<AccountSettings["profile"]>) {
    this.profile = { ...this.profile, ...profile };
  }

  getWallet(cur: string) {
    return this.wallets.find(w => w.coin === cur)?.address ?? ""
  }

  async changePassword(pwd: string, newPwd: string) {
    await UsersService.settingsApiV1UsersSettingsPatch({
      current_password: pwd,
      new_password: newPwd,
      email: this.profile.email,
    });
  }

  async setWallet() {
    await WalletsService.addSupportedCurrencyToClientApiV1WalletsConnectWalletPost({
      coin: "USN",
      address: "hello",
      network: "mainnet"
    })
  }

  async changeEmail(pwd: string, email: string) {
    await UsersService.settingsApiV1UsersSettingsPatch({
      current_password: pwd,
      email: email,
    });

    this.changeProfile({ email });
  }

  async fetchProfile() {
    const profile = await UsersService.getProfileApiV1UsersProfileGet();
    const wallets = await WalletsService.listWalletsApiV1WalletsListGet();

    runInAction(() => {
      this.wallets = wallets
      this.changeProfile({
        companyCountry: profile.info.company_country_of_residence,
        companyName: profile.info.company_name,
        username: profile.user_name,
        email: profile.email,
      });
    })
  }
}

export default AccountSettings;
