import { RepeatIcon, CopyIcon } from "@chakra-ui/icons";
import { Box, Button, ButtonGroup, Heading, SimpleGrid, Tag, TagLabel, Text, VStack } from "@chakra-ui/react";
import { observer } from "mobx-react-lite";
import { useCompany } from "../../core/useApp";
import { showToast } from "../../uikit/tost";
import CreateIntegration from "./CreateIntegration";
import * as S from "./styled";

const Integrations = () => {
  const { integrations } = useCompany();
  const copyClient = async (id: string) => {
    await navigator.clipboard.writeText(id);
    showToast("Client ID copied");
  };

  return (
    <>
      <S.PageHeader>
        <h2>Merchants list</h2>
        <CreateIntegration model={integrations} />
      </S.PageHeader>

      <SimpleGrid columns={3} spacing={8} mt={8} minChildWidth="300px">
        {integrations.list.map((client) => (
          <Box
            bg="white"
            height={200}
            boxShadow="0 0 8px 0 rgba(0, 0, 0, .05)"
            rounded="2xl"
            key={client.client_id}
            pos="relative"
            padding={4}
            flexDir="column"
            display="flex"
            justifyContent="space-between"
            transition="0.2s transform"
            _hover={{
              transform: "scale(1.02)",
            }}
          >
            <div>
              <Heading fontSize="2xl" m="0">
                {client.name}
              </Heading>
              <Text>{client.info.description}</Text>
              <Text color="gray">{client.info.site}</Text>
            </div>

            <VStack mt={4} spacing={2} alignItems="flex-start">
              <Button size="xs" rounded="full" leftIcon={<CopyIcon />} onClick={() => copyClient(client.client_id)}>
                ID: {client.client_id.slice(0, 8)}...
              </Button>

              <ButtonGroup size="xs" isAttached variant="outline">
                <Button>Secret: {client.client_secret.slice(0, 5)}</Button>
                <Button
                  leftIcon={<RepeatIcon />}
                  isLoading={client.loading}
                  loadingText="Generating..."
                  onClick={() => integrations.generateSecret(client.id)}
                >
                  Generate new
                </Button>
              </ButtonGroup>
            </VStack>

            <Tag size="lg" colorScheme="gray" borderRadius="full" pos="absolute" right={4} top={4}>
              <TagLabel>Pending</TagLabel>
            </Tag>
          </Box>
        ))}
      </SimpleGrid>
    </>
  );
};

export default observer(Integrations);
