import { action, autorun, makeObservable, observable, runInAction } from "mobx";
import { BeneficiariesService, OpenAPI, RetrieveBeneficiarySchema } from "../generated";
import Currencies from "./Currencies";
import IncomeModel from "./Income";
import Profile from "./Profile";
import Auth from "./Auth";
import Integrations from "./Integrations";

export class CompanyStore {
  settings = new Profile();
  income = new IncomeModel();
  integrations = new Integrations(this);

  constructor(readonly model: RetrieveBeneficiarySchema) {
    makeObservable(this, {})
  }

  async fetch() {
    this.settings.fetchProfile();
    this.income.refresh()
  }
}

export class AccountStore {
  companies: CompanyStore[] = []
  company: CompanyStore | null = null
  currencies = new Currencies();

  constructor() {
    makeObservable(this, { company: observable, companies: observable, selectCompany: action })
    this.fetchList()
  }

  async fetchList() {
    const list = await BeneficiariesService.listBeneficiariesApiV1BeneficiariesGet()
    runInAction(() => {
      this.companies = list.map(p => new CompanyStore(p))
      this.selectCompany(this.companies[0])
    })
  }

  selectCompany(company: CompanyStore) {
    console.log(company, 'select')
    this.company = company
    this.company.fetch()
  }

  async createCompany(name: string, currency: string, country: string) {
    const data = { name, country_of_residence: country, local_currency: currency }
    const company = await BeneficiariesService.createBeneficiaryApiV1BeneficiariesPost(data)
    runInAction(() => {
      const model = new CompanyStore(company)
      this.companies.push(model)
      this.selectCompany(model)
    })
  }

  dispose() { }
}

class App {
  auth = new Auth();
  account: AccountStore | null = null;

  constructor() {
    makeObservable(this, {
      account: observable,
      addAccount: action,
      removeAccount: action,
    });

    OpenAPI.BASE = process.env.REACT_APP_API ?? "";
    autorun(() => {
      if (this.auth.isAuth) this.addAccount();
      else this.removeAccount();
    });
  }

  addAccount() {
    this.account = new AccountStore();
  }

  removeAccount() {
    this.account?.dispose();
    this.account = null;
  }
}

export default App;
