import { makeObservable, observable, runInAction } from "mobx";
import { CurrenciesService, ListCurrenciesSchema } from "../generated";

class Currencies {
  public list: ListCurrenciesSchema[] = [];

  constructor() {
    makeObservable(this, { list: observable });
    this.fetchCurrencies();
  }

  async fetchCurrencies() {
    const currencies = await CurrenciesService.getSupportedCurrenciesApiV1CurrencyGet();
    runInAction(() => {
      this.list = currencies;
      console.log(currencies)
    });
  }
}

export default Currencies;
