import { FC } from "react";
import { useForm } from "react-hook-form";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  Button,
  FormControl,
  FormLabel,
  Input,
  Select,
} from "@chakra-ui/react";
import { AccountStore } from "../../core/App";

interface Props {
  model: AccountStore;
  onClose: () => void;
  onOpen: () => void;
  isOpen: boolean;
}

const CreateCompanyWidget: FC<Props> = ({ model, onClose, onOpen, isOpen }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<{ name: string; country: string; currency: string }>();

  const handleCreate = handleSubmit(async (data) => {
    await model.createCompany(data.name, data.currency, data.country);
    onClose();
    reset();
  });

  return (
    <Modal isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay background="blackAlpha.200" backdropFilter="blur(20px)" />
      <ModalContent borderRadius={22}>
        <ModalHeader>Create company</ModalHeader>
        <ModalBody>
          <form onSubmit={handleCreate}>
            <FormControl isInvalid={errors.name != null}>
              <FormLabel htmlFor="name">Company name</FormLabel>
              <Input id="name" placeholder="Name" {...register("name", { required: "This is required" })} />
            </FormControl>

            <FormControl isInvalid={errors.currency != null} mt={4}>
              <FormLabel htmlFor="currency">Currency</FormLabel>
              <Select
                id="currency"
                placeholder="Select currency"
                {...register("currency", { required: "This is required" })}
              >
                <option value="USD">USD</option>
                <option value="EUR">EUR</option>
              </Select>
            </FormControl>

            <FormControl isInvalid={errors.country != null} mt={4}>
              <FormLabel htmlFor="country">Country</FormLabel>
              <Input id="country" placeholder="USA" {...register("country", { required: "This is required" })} />
            </FormControl>
          </form>
        </ModalBody>

        <ModalFooter>
          <Button isLoading={isSubmitting} loadingText="Creating..." colorScheme="purple" onClick={handleCreate}>
            Create
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

export default CreateCompanyWidget;
