import Popup from "reactjs-popup";
import { Button, useDisclosure } from "@chakra-ui/react";
import { CheckIcon, AddIcon, ChevronDownIcon } from "@chakra-ui/icons";

import { observer } from "mobx-react-lite";
import { PopupActions } from "reactjs-popup/dist/types";
import { useRef } from "react";

import { useApp } from "../../core/useApp";
import * as S from "./styled";
import CreateCompanyWidget from "./CreateCompanyWidget";

const CompaniesPicker = () => {
  const { account } = useApp();
  const ref = useRef<PopupActions>(null);
  const widgetControl = useDisclosure();

  const company = account?.company;
  if (account == null) return null;

  const trigger = (
    <Button variant="ghost">
      {company?.model.name}
      <ChevronDownIcon />
    </Button>
  );

  return (
    <S.CompanyBadge>
      <CreateCompanyWidget model={account} {...widgetControl} />
      <Popup ref={ref} position="bottom right" trigger={trigger} offsetY={8}>
        <S.Tooltip>
          {account?.companies.map((comp) => (
            <Button
              size="lg"
              width="100%"
              borderRadius={0}
              justifyContent="flex-start"
              key={comp.model.id}
              leftIcon={company === comp ? <CheckIcon /> : undefined}
              onClick={() => {
                account?.selectCompany(comp);
                ref.current?.close();
              }}
            >
              {comp.model.name}
            </Button>
          ))}

          <Button
            size="lg"
            width="100%"
            borderRadius={0}
            justifyContent="flex-start"
            leftIcon={<AddIcon />}
            onClick={() => {
              widgetControl.onOpen();
              ref.current?.close();
            }}
          >
            Create company
          </Button>
        </S.Tooltip>
      </Popup>
    </S.CompanyBadge>
  );
};

export default observer(CompaniesPicker);
