import { FC } from "react";
import { useForm } from "react-hook-form";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  Button,
  FormControl,
  FormLabel,
  Input,
  useDisclosure,
  Textarea,
  FormHelperText,
  Link,
} from "@chakra-ui/react";
import Integrations from "../../core/Integrations";

interface Props {
  model: Integrations;
}

const CreateIntegration: FC<Props> = ({ model }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<{ name: string; description: string; site: string; callback_url: string }>();
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleCreate = handleSubmit(async (data) => {
    await model.create(data);
    onClose();
    reset();
  });

  return (
    <>
      <Button colorScheme="purple" onClick={onOpen}>
        New integration
      </Button>
      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay background="blackAlpha.200" backdropFilter="blur(20px)" />
        <ModalContent borderRadius={22}>
          <ModalHeader>Create integration</ModalHeader>
          <ModalBody>
            <form onSubmit={handleCreate}>
              <FormControl isInvalid={errors.name != null}>
                <FormLabel htmlFor="name">Integration name</FormLabel>
                <Input id="name" placeholder="Name" {...register("name", { required: "This is required" })} />
              </FormControl>

              <FormControl mt={4}>
                <FormLabel htmlFor="site">Site</FormLabel>
                <Input id="site" placeholder="https://google.com" {...register("site")} />
                <FormHelperText>Tell us about your project website (optional)</FormHelperText>
              </FormControl>

              <FormControl mt={4}>
                <FormLabel htmlFor="description">Description</FormLabel>
                <Textarea id="description" placeholder="Description" {...register("description")} />
                <FormHelperText>
                  Tell us more about your business. For example what kind of goods you sell. It will help us approve
                  your integration faster. (optional)
                </FormHelperText>
              </FormControl>

              <FormControl mt={4}>
                <FormLabel htmlFor="callback_url">Callback url</FormLabel>
                <Input id="callback_url" placeholder="https://google.com" {...register("callback_url")} />
                <FormHelperText>
                  We will send callback to this URL after each successful payment.{" "}
                  <Link href="https://docs.paywithnear.com/api/handling-callbacks/">Read more about callbacks.</Link>
                </FormHelperText>
              </FormControl>
            </form>
          </ModalBody>

          <ModalFooter>
            <Button isLoading={isSubmitting} loadingText="Creating..." colorScheme="purple" onClick={handleCreate}>
              Create
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
};

export default CreateIntegration;
