import { useState } from "react";
import { QRCodeSVG } from "qrcode.react";
import { useForm } from "react-hook-form";
import { CloseIcon, CopyIcon } from "@chakra-ui/icons";
import * as Kit from "@chakra-ui/react";

import { CreatePaymentsRequestSchema } from "../../generated";
import IncomeModel from "../../core/Income";
import { showToast } from "../../uikit/tost";

const CreatePaymentWidget = ({ model }: { model: IncomeModel }) => {
  const { isOpen, onOpen, onClose } = Kit.useDisclosure();
  const [payment, setPayment] = useState<string | null>(null);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isSubmitting },
  } = useForm<CreatePaymentsRequestSchema>();

  const handleCreate = handleSubmit(async (data) => {
    const link = await model.createPayment(data);
    setPayment(link);
    reset();
  });

  const handleClose = () => {
    setPayment(null);
    onClose();
  };

  const copyPayment = async () => {
    if (payment == null) return;
    await navigator.clipboard.writeText(payment);
    showToast("Payment link copied");
  };

  if (payment) {
    return (
      <>
        <Kit.Button colorScheme="purple" onClick={onOpen}>
          Payment
        </Kit.Button>
        <Kit.Modal isOpen={isOpen} onClose={handleClose} isCentered>
          <Kit.ModalOverlay background="blackAlpha.200" backdropFilter="blur(20px)" />
          <Kit.ModalContent borderRadius={22} width="fit-content">
            <Kit.ModalBody padding={8}>
              <QRCodeSVG value={payment} size={200} />

              <Kit.HStack mt={4} spacing={4}>
                <Kit.Button width="100%" leftIcon={<CopyIcon />} onClick={copyPayment}>
                  Copy link
                </Kit.Button>
                <Kit.Button onClick={handleClose}>
                  <CloseIcon />
                </Kit.Button>
              </Kit.HStack>
            </Kit.ModalBody>
          </Kit.ModalContent>
        </Kit.Modal>
      </>
    );
  }

  return (
    <>
      <Kit.Button colorScheme="purple" onClick={onOpen}>
        Create payment
      </Kit.Button>
      <Kit.Modal isOpen={isOpen} onClose={onClose} isCentered>
        <Kit.ModalOverlay background="blackAlpha.200" backdropFilter="blur(20px)" />
        <Kit.ModalContent borderRadius={22}>
          <Kit.ModalHeader>Create payment</Kit.ModalHeader>
          <Kit.ModalBody>
            <form onSubmit={handleCreate}>
              <Kit.FormControl isInvalid={errors.name != null}>
                <Kit.FormLabel htmlFor="name">Payment name</Kit.FormLabel>
                <Kit.Input id="name" placeholder="Cool socks" {...register("name", { required: true })} />
              </Kit.FormControl>

              <Kit.HStack spacing={4} mt={4}>
                <Kit.FormControl isInvalid={errors.amount_usd != null} m={0}>
                  <Kit.FormLabel htmlFor="amount">Amount</Kit.FormLabel>
                  <Kit.NumberInput defaultValue={0} min={0} max={1000000}>
                    <Kit.NumberInputField
                      id="amount"
                      placeholder="10$"
                      {...register("amount_usd", { required: true, valueAsNumber: true })}
                    />
                    <Kit.NumberInputStepper>
                      <Kit.NumberIncrementStepper />
                      <Kit.NumberDecrementStepper />
                    </Kit.NumberInputStepper>
                  </Kit.NumberInput>
                </Kit.FormControl>

                <Kit.FormControl isInvalid={errors.coin != null} m={0}>
                  <Kit.FormLabel htmlFor="coin">Coin</Kit.FormLabel>
                  <Kit.Select
                    id="coin"
                    defaultValue="NEAR"
                    placeholder="Select coin"
                    {...register("coin", { required: "This is required" })}
                  >
                    <option value="NEAR">NEAR</option>
                    <option value="USN">USN</option>
                  </Kit.Select>
                </Kit.FormControl>
              </Kit.HStack>

              <Kit.FormControl isInvalid={errors.return_url != null} mt={4}>
                <Kit.FormLabel htmlFor="return_url">Return url</Kit.FormLabel>
                <Kit.Input
                  id="return_url"
                  placeholder="Return url"
                  value="https://paywithnear.com"
                  {...register("return_url", { required: true })}
                />
              </Kit.FormControl>

              <Kit.FormControl isInvalid={errors.return_url != null} mt={4}>
                <Kit.FormLabel htmlFor="description">Description</Kit.FormLabel>
                <Kit.Textarea id="description" placeholder="Description" {...register("description")} />
              </Kit.FormControl>
            </form>
          </Kit.ModalBody>

          <Kit.ModalFooter>
            <Kit.Button isLoading={isSubmitting} loadingText="Creating..." colorScheme="purple" onClick={handleCreate}>
              Create
            </Kit.Button>
          </Kit.ModalFooter>
        </Kit.ModalContent>
      </Kit.Modal>
    </>
  );
};

export default CreatePaymentWidget;
