import { useForm } from "react-hook-form";
import { observer } from "mobx-react-lite";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  Button,
  FormControl,
  FormLabel,
  Input,
  useDisclosure,
} from "@chakra-ui/react";
import { useCompany } from "../../core/useApp";
import { showToast } from "../../uikit/tost";

const ChangePassword = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { settings } = useCompany();

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<{ newpwd: string; oldpwd: string }>();

  const handleLogin = handleSubmit(async ({ newpwd, oldpwd }) => {
    try {
      await settings.changePassword(oldpwd, newpwd);
      showToast("Password was changed");
      onClose();
    } catch {
      showToast("Something wrong, check fields");
    }
  });

  return (
    <>
      <Button colorScheme="purple" onClick={onOpen}>
        Change password
      </Button>
      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay background="blackAlpha.200" backdropFilter="blur(20px)" />
        <ModalContent borderRadius={22}>
          <ModalHeader>Create payment</ModalHeader>
          <ModalBody>
            <form onSubmit={handleLogin}>
              <FormControl isInvalid={errors.newpwd != null}>
                <FormLabel htmlFor="oldpwd" m={0}>
                  Current password
                </FormLabel>
                <Input id="oldpwd" placeholder="Password" type="password" {...register("oldpwd", { required: true })} />
              </FormControl>

              <FormControl isInvalid={errors.oldpwd != null} mt={4}>
                <FormLabel htmlFor="newpwd" m={0}>
                  New password
                </FormLabel>
                <Input id="newpwd" placeholder="Password" type="password" {...register("newpwd", { required: true })} />
              </FormControl>
            </form>
          </ModalBody>

          <ModalFooter>
            <Button isLoading={isSubmitting} colorScheme="purple" onClick={handleLogin}>
              Apply
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
};

export default observer(ChangePassword);
